import { MenuItem, TextField, Button } from '@material-ui/core';
import React, { useState } from 'react';
import { useHistory } from "react-router";

import ErrorMessage from '../components/ErrorMessage';

import Categories from '../Data/Categories';
import './Home.css';


const Home = ({ name, setName, fetchQuestions }) => {


  const [category, setCategory] = useState("");
  const [difficulty, setDifficulty] = useState("");
  const [error, setError] = useState(false);

  const history = useHistory();

  const handleSubmit = () => {
    if (!category || !difficulty || !name) {
      setError(true);
      return;
    } else {
      setError(false);
      fetchQuestions(category, difficulty);
      history.push("./quiz");//problem?

    }
  }

  return (
    <div className="content">
      <div className="settings">
        <span>Fill All Fields</span>


        <div className="settings-select">
          {error && <ErrorMessage>Please fill all the fields.</ErrorMessage>}
          <TextField style={{ marginBottom: 25 }} label="Name" variant="outlined" onChange={(e) => setName(e.target.value)} />
          <TextField style={{ marginBottom: 30 }} select label="Select Category" variant="outlined" onChange={(e) => setCategory(e.target.value)} value={category}>
            {Categories.map((cat) => (
              <MenuItem key={cat.category} value={cat.value}>
                {cat.category}
              </MenuItem>
            ))
            }

          </TextField>
          <TextField
            select
            label="Select Difficulty"
            variant="outlined"
            style={{ marginBottom: 30 }}
            onChange={(e) => setDifficulty(e.target.value)} value={difficulty}
          >
            <MenuItem key="Easy" value="easy">
              Easy
            </MenuItem>
            <MenuItem key="Medium" value="medium">
              Medium
            </MenuItem>
            <MenuItem key="Hard" value="hard">
              Hard
            </MenuItem>
          </TextField>

          <Button
            variant="contained"
            color="inherit"
            size="large"
            onClick={handleSubmit}
          >
            Start
          </Button>
        </div>
      </div>
      <div ><iframe src="https://giphy.com/embed/U3NztlDraQh716nt6z" src="https://giphy.com/embed/U3NztlDraQh716nt6z" allowFullScreen></iframe></div><p><a href="https://giphy.com/gifs/illusion-optical-opticalillusion-U3NztlDraQh716nt6z"></a></p>        </div>
  )
}
export default Home;
